$color-white: #FFFFFF;

// Social Colors
$color-facebook: #2D5F9A;
$color-twitter: #00C3F3;
$color-google: #D73D32;
$color-pinterest: #CD252B;
$color-reddit: #FF5700;
$color-linkedin: #0077B5;
