$color-youtube: #f00;
$color-telegram: #0693ec;

.hustle-icon-outline  {
	border-radius: 40px;

	.hustle-icon {
		width: 16px;
		height: 16px;
	}

	// Colorize Social Icons
	&.hustle-icon-facebook {
		border-color: $color-facebook;

		.hustle-icon-path {
			fill: $color-facebook;
		}

		.hustle-shares-counter {
			color: $color-white;
		}
	}
	&.hustle-icon-twitter {
