.wpmudev-ui .wpmudev-box-modal {

	.wpmudev-box-head {
		display: flex;
		flex-wrap: wrap;
		flex-direction: row;
		align-items: center;
		padding: 15px 23px 15px 30px;
		border-bottom: 1px solid $color-silver--alt;
		border-radius: 5px 5px 0 0;
		background: $color-white;

		h2, h3 {
			flex: 1;
			margin-right: 30px;
			line-height: 30px;

			&:last-child {
				margin-right: 0;
			}
		}

		h2 {
			margin: 0;
			color: $color-gray--dark;
			font-size: 18px;
			letter-spacing: -0.45px;
			text-transform: uppercase;
		}
		
		h3 {
			margin: 0;
			font-size: 15px;
			letter-spacing: -0.15px;
			text-transform: uppercase;
		}

		.wpmudev-icon {
			max-width: 18px;
			max-height: 18px;
			margin-right: 20px;

			&.wpmudev-i_close {
				max-width: 13px;
				max-height: 13px;
				cursor: pointer;

				path {
					fill: rgba(51,51,51,0.5);
				}
			}

			&.wpmudev-i_minus {
				max-width: 13px;
				max-height: 2px;
				cursor: pointer;

				path {
					fill: rgba(51,51,51,0.5);
				}
			}

			&:last-child {
				margin-right: 0;
			}
		}

		.wpmudev-box-action {
			cursor: pointer;
			padding: 5px 0;

			.wpmudev-icon {
				display: block;
			}
		}

		.wpmudev-box-reset {
			display: flex;
			flex: 1;
			flex-wrap: wrap;
			flex-direction: column;
			margin-right: 20px;

			h2, h3 {
				flex: 0 0 auto;
				margin-right: 20px;

				&:last-child {
					margin-right: 0;
				}
			}

			.wpmudev-button {
				flex: 0 0 auto;
				margin-right: 20px;

				&:last-child {
					margin-right: 0;
				}

				@include screen("small") {
					margin-right: 20px;
				}
			}

			&:last-child {
				margin-right: 0;
			}

			@include screen("xs") {
				flex-direction: row;
				align-items: center;
			}
		}
	}

	.wpmudev-box-body {
		padding: 20px 21px;
		background: $color-white;

		&:only-child {
			border-radius: 5px;
		}

		&:first-child:not(:only-child) {
			border-radius: 5px 5px 0 0;
		}

		&:last-child:not(:only-child) {
			border-radius: 0 0 5px 5px;
		}

		@include screen("small") {
			padding: 30px 31px;
		}
	}

	.wpmudev-box-footer {
		display: flex;
		flex-wrap: wrap;
		flex-direction: column;
		padding: 20px;
		border-top: 1px solid $color-silver--alt;
		background: $color-white;

		.wpmudev-button {
			margin: 0 0 20px;

			&:last-child {
				margin: 0;
			}

			@include screen("small") {
				margin: 0;
			}
		}

		&:only-child {
			border-radius: 5px;
		}

		&:first-child:not(:only-child) {
			border-radius: 5px 5px 0 0;
		}

		&:last-child:not(:only-child) {
			border-radius: 0 0 5px 5px;
		}

		@include screen("small") {
			flex-direction: row;
			align-items: center;
			justify-content: space-between;
			padding: 30px;
		}
	}
}

.wpmudev-ui .wpmudev-modal-upsell {

	.wpmudev-box-modal {
		position: relative;
		padding: 20px;
		border-radius: 4px;
		background-color: $color-white;

		.wpmudev-box-head {
			padding: 0;
			border-bottom: 0;
			position: absolute;
			top: 20px;
			right: 20px;

			+ .wpmudev-box-body {
				margin-top: 20px;

				@include screen("small") {
					margin-top: 30px;
				}
			}
		}

		.wpmudev-box-body {
			padding: 0;
			background-color: transparent;
			text-align: center;

			h2 {
				margin: 0 0 20px;
				padding: 0;
				color: $color-gray--dark;
				font-size: 22px;
				line-height: 30px;
				font-family: $font--alt;
				font-weight: bold;
				letter-spacing: -0.25px;
	
				&:last-child {
					margin-bottom: 0;
				}
			}
	
			p {
				margin: 0 0 30px;
				padding: 0;
				color: $color-gray;
				font-size: 15px;
				line-height: 30px;
				font-family: $font;
				font-weight: 400;
				letter-spacing: -0.25px;
			}

			img {
				display: block;
				margin: 20px auto -20px;

				@include screen("small") {
					margin-top: 30px;
					margin-bottom: -30px;
				}
			}

			.wpmudev-button {
				margin: 0;

				&.wpmudev-button-sm {
					padding: 7px 16px;
					font-size: 12px;
					line-height: 16px;
					font-family: $font;
					font-weight: 500;
					letter-spacing: -0.25px;
				}
			}
		}

		@include screen("small") {
			padding: 30px;
		}
	}
}

.wpmudev-ui .wpmudev-modal:not(.wpmudev-modal-upsell) {

	.wpmudev-box-modal {

		.wpmudev-box-body {
			max-height: 60px;
			overflow-y: auto;

			@media (min-height: 410px) {
				max-height: 120px;
			}
	
			@media (min-height: 570px) {
				max-height: 300px;
			}
	
			@media (min-height: 800px) {
				max-height: 520px;
			}
	
			@media (min-height: 980px) {
				max-height: 620px;
			}
	
			@media (min-height: 1100px) {
				max-height: 700px;
			}
		}
	}
}