$hui-unsub--transition:          0.3s ease;

$hui-unsub-form--gutter:         30px;
$hui-unsub-form--background:     #5D7380;
$hui-unsub-form--color:          #FFFFFF;
$hui-unsub-form--font-size:      15px;
$hui-unsub-form--line-height:    22px;
$hui-unsub-form--font-weight:    500;
$hui-unsub-form--font-family:    $font;

$hui-unsub-error--padding:       10px;
$hui-unsub-error--background:    #FFE5E9;
$hui-unsub-error--color:         #FF6D6D;
$hui-unsub-error--font-size:     13px;
$hui-unsub-error--line-height:   20px;
$hui-unsub-error--font-family:   $font;
$hui-unsub-error--font-weight:   500;

$hui-unsub-input--height:        36px;
$hui-unsub-input--padding:       10px;
$hui-unsub-input--border-width:  2px;
$hui-unsub-input--border-style:  solid;
$hui-unsub-input--border-color:  transparent;
$hui-unsub-input--border-radius: 0;
$hui-unsub-input--background:    #FFFFFF;
$hui-unsub-input--color:         #363B3F;
$hui-unsub-input--placeholder:   #ADB5B7;
$hui-unsub-input--font-size:     13px;
$hui-unsub-input--line-height:   18px;
$hui-unsub-input--font-family:   $font;
$hui-unsub-input--font-weight:   400;

$hui-unsub-button--height:            36px;
$hui-unsub-button--padding:           10px;
$hui-unsub-button--border-radius:     0;
$hui-unsub-button--background:        #38C5B5;
$hui-unsub-button--background-active: #49E2D1;
$hui-unsub-button--color:             #FFFFFF;
$hui-unsub-button--font-size:         13px;
$hui-unsub-button--line-height:       18px;
$hui-unsub-button--font-family:       $font;
$hui-unsub-button--font-weight:       500;

$hui-unsub-checkbox--size:       20px;
$hui-unsub-checkbox--background: #FFFFFF;
$hui-unsub-checkbox--icon-color: #38C5B5;
$hui-unsub-checkbox--icon-size:  14px;

.hustle-unsubscribe-form {
	padding: $hui-unsub-form--gutter;
	background-color: $hui-unsub-form--background;

	* {
		box-sizing: border-box;
		text-rendering: geometricPrecision;
		-webkit-font-smoothing: antialiased;
	}

	.wpoi-submit-failure {
		display: block;
		margin-bottom: #{$hui-unsub-form--gutter / 2};
		padding: $hui-unsub-error--padding;
		background-color: $hui-unsub-error--background;
		color: $hui-unsub-error--color;
		font: $hui-unsub-error--font-weight #{$hui-unsub-error--font-size}/#{$hui-unsub-error--line-height} $hui-unsub-error--font-family;

		~ .hustle-form-body {

			.hustle-email-section input {
				border-color: $hui-unsub-error--color;
			}
		}
	}

	.hustle-form-body {
		display: block;
		color: $hui-unsub-form--color;
		font: $hui-unsub-form--font-weight #{$hui-unsub-form--font-size}/#{$hui-unsub-form--line-height} $hui-unsub-form--font-family;

		.hustle-email-lists label {
			cursor: pointer;
			display: block;
			margin: 0 0 #{$hui-unsub-form--gutter / 2};

			input {
				@extend %screen-reader-only;

				&:checked + span:before {
					color: $hui-unsub-checkbox--icon-color;
				}
			}

			span {
				display: block;
				position: relative;
				padding-left: #{$hui-unsub-checkbox--size + 10px};
				color: #FFFFFF;
				font: 400 13px/#{$hui-unsub-checkbox--size} $font;

				&:before {
					content: "\e925";
					width: $hui-unsub-checkbox--size;
					height: $hui-unsub-checkbox--size;
					display: block;
					position: absolute;
					top: 0;
					left: 0;
					background-color: $hui-unsub-checkbox--background;
					color: $hui-unsub-checkbox--background;
					font-size: $hui-unsub-checkbox--icon-size;
					line-height: $hui-unsub-checkbox--size;
					font-family: "wpdui-fi" !important;
					text-align: center;
				}
			}

			&:last-child {
				margin: 0;
			}
		}

		.hustle-email-section {

			input {
				width: 100%;
				height: $hui-unsub-input--height;
				display: block;
				padding: #{($hui-unsub-input--height - $hui-unsub-input--line-height - $hui-unsub-input--border-width) / 2} #{$hui-unsub-input--padding - $hui-unsub-input--border-width};
				border-width: $hui-unsub-input--border-width;
				border-style: $hui-unsub-input--border-style;
				border-color: $hui-unsub-input--border-color;
				border-radius: $hui-unsub-input--border-radius;
				background-color: $hui-unsub-input--background;
				color: $hui-unsub-input--color;
				font: $hui-unsub-input--font-weight #{$hui-unsub-input--font-size}/#{$hui-unsub-input--line-height} $hui-unsub-input--font-family;
				transition: $hui-unsub--transition;

				&:-moz-placeholder {
					color: $hui-unsub-input--placeholder;
				}
			
				&::-moz-placeholder {
					color: $hui-unsub-input--placeholder;
				}
	
				&:-ms-input-placeholder {
					color: $hui-unsub-input--placeholder;
				}
	
				&::-webkit-input-placeholder {
					color: $hui-unsub-input--placeholder;
				}
			}
		}

		.hustle-unsub-button {
			width: auto;
			display: block;
			margin: #{$hui-unsub-form--gutter / 2} 0 0;
			padding: $hui-unsub-button--padding;
			border: 0;
			border-radius: $hui-unsub-button--border-radius;
			background-color: $hui-unsub-button--background;
			color: $hui-unsub-button--color;
			font: $hui-unsub-button--font-weight #{$hui-unsub-button--font-size}/#{$hui-unsub-button--line-height} $hui-unsub-button--font-family;
			text-align: center;
			transition: $hui-unsub--transition;

			&:hover,
			&:active {
				background-color: $hui-unsub-button--background-active;
			}
		}
	}
}