.wpmudev-ui .wpmudev-box-listing {
	margin-bottom: 20px;
	box-shadow: 0 2px 0 $color-silver--alt;
	border-radius: 5px;
	background: $color-white;

	.wpmudev-box-head {
		cursor: pointer;
		display: flex;
		padding: 20px;

		.wpmudev-box-group {
			display: flex;
			flex: 1;
			margin-right: 20px;

			.wpmudev-group-switch {
				flex: 0 0 auto;

				.wpmudev-switch {
					width: 40px;
					display: block;
					margin: 0;
					padding: 0;
				}

				+ .wpmudev-group-title {
					margin-left: 10px;

					@include screen("small") {
						margin-left: 10px;
					}
				}
			}

			.wpmudev-group-title {
				display: flex;
				flex: 0 0 auto;
				margin-right: 10px;

				h5 {
					margin: 0;
					line-height: 30px;

					+ .wpmudev-helper {
						margin-left: 10px;
					}
				}

				.wpmudev-helper {
					display: none;
					padding: 3px 0 1px;
					line-height: 26px;

					@include screen("small") {
						display: block;
					}
				}

				@include screen("small") {
					margin-right: 20px;
				}
			}

			.hustle-delete-module-confirmation {
				display: flex;
				flex: 0 0 auto;
				align-items: center;
				
				label {
					display: block;
					flex: 0 0 auto;
					margin-right: 10px;
				}

				.hustle-delete-module-confirm,
				.hustle-delete-module-cancel {
					min-width: 40px;
					flex: 0 0 auto;
					margin-right: 10px;
					padding: 0 5px;

					&:last-child {
						margin-right: 10px;

						@include screen("small") {
							margin-right: 0;
						}
					}

					@include screen("small") {
						min-width: 40px;
						margin-right: 10px;
						padding: 0 5px;
					}
				}
			}

			.wpmudev-group-buttons {
				display: flex;
				flex: 0 0 auto;
				flex-wrap: wrap;

				.wpmudev-button {
					display: block;
					flex: 0 0 auto;
					margin-right: 10px;

					&:last-child {
						margin-right: 0;
						
						@include screen("small") {
							margin-right: 0;
						}
					}

					@include screen("small") {
						margin-right: 10px;
					}
				}

				+ .hustle-delete-module-confirmation {
					margin-left: 10px;
				}
			}

			.wpmudev-box-group--inner {
				display: flex;
				flex: 1;
				flex-wrap: wrap;
				align-items: center;

				.wpmudev-group-buttons {

					.wpmudev-button {

						&:last-child {

							@include screen("small") {
								margin-right: 0;
							}
						}

						@include screen("small") {
							margin-right: 10px;
						}
					}
				}

				+ .wpmudev-group-buttons {
					margin-left: 10px;
					
					@include screen("small") {
						margin-left: 10px;
					}
				}
			}

			@include screen("small") {
				flex-direction: row;
				align-items: center;
				margin-right: 28px;
			}
		}

		.wpmudev-element--settings {
			flex: 0 0 auto;
			position: absolute;
			top: 10px;
			right: 10px;
			overflow: inherit !important;

			.wpmudev-element--title {
				display: none;
			}

			.wpmudev-element--content {
				overflow: inherit !important;
			}

			@include screen("small") {
				top: 20px;
				right: 20px;
			}

			@include screen("large") {
				flex: 0 0 30px;
				position: relative;
				top: auto;
				//right: auto;
			}
		}
		
		.wpmudev-dots-dropdown {
			position: relative;

			.wpmudev-dots-button {
				width: 30px;
				height: 30px;
				cursor: pointer;
				display: flex;
				flex-direction: column;
				align-items: center;
				justify-content: center;
				padding: 0;
				border: 1px solid #E6E6E6;
				border-radius: 3px;
				background: $color-white;
				box-shadow: none;
				outline: none;
				transition: 0.2s ease;

				svg {
					display: block;
				}

				svg circle {
					transition: 0.2s ease;
				}

				&:hover {
					background: #F2F2F2;
				}

				&.wpmudev-active {
					background: #F2F2F2;

					svg circle {
						fill: $color-blue;
					}
				}
			}

			.wpmudev-dots-nav {
				min-width: 180px;
				position: absolute;
				top: 100%;
				right: 0;
				margin: 10px 0 0;
				padding: 10px;
				border: 1px solid #CCC;
				border-radius: 3px;
				background: $color-white;
				box-shadow: 0 1px 5px 0 rgba(0,0,0,0.15);
				z-index: 2;

				li {
					margin: 0;
					padding: 0;
					font-size: 13px;
					line-height: 30px;
					
					&:before {
						display: none;
					}

					a {
						cursor: pointer;
						display: block;
						padding: 0 10px;
						border-radius: 2px;
						
						&, &:visited {
							color: #333333;
						}

						&:hover, &:active, &:focus {
							color: $color-white;
							background: $color-blue;
						}
					}

					&:last-child {
						border-top: 1px solid #E9E9E9;

						a {
							
							&, &:visited {
								color: $color-red;
							}

							&:hover, &:active, &:focus {
								color: $color-white;
								background: $color-red;
							}
						}
					}
				}

				&.wpmudev-hide {
					display: none;
				}

				@include screen("large") {
					right: 0;
					margin: 5px 0 0;
				}
			}
		}

		.wpmudev-box-action {
			min-width: 12px;
			cursor: pointer;
			display: flex;
			flex: 0 0 12px;
			flex-direction: column;
			align-content: center;
			justify-content: center;
			padding: 8px 0;
			
			.wpmudev-icon {
				width: 12px;

				path {
					fill: $color-silver--dark;
				}

				&.wpmudev-i_arrow {
					max-height: 8px;
				}
			}

			&:hover,
			&:focus {

				.wpmudev-icon path {
					fill: $color-gray--light;
				}
			}
		}

		// Button: Edit
		.hustle-edit-module {
			min-width: 40px;
			padding: 0;

			.wpmudev-button-icon {
				height: 30px;
				display: flex;
				flex-direction: column;
				align-items: center;
				justify-content: center;

				.wpmudev-icon {
					width: 14px;
					height: 14px;

					path {
						fill: $color-white;
					}
				}

				@include screen("small") {
					display: none;
				}
			}

			.wpmudev-button-text {
				display: none;

				@include screen("small") {
					display: block;
					text-align: center;
				}
			}

			@include screen("small") {
				min-width: 60px;
				padding: 0 16px;
			}
		}

		// Button: Delete
		.hustle-delete-module {
			min-width: 40px;
			padding: 0;

			.wpmudev-button-icon {
				height: 30px;
				display: flex;
				flex-direction: column;
				align-items: center;
				justify-content: center;
				position: relative;

				.wpmudev-icon {
					width: 20px;
					height: 20px;

					path {
						fill: $color-red--light;
					}
				}

				&:before {
					content: " ";
					width: 18px;
					height: 18px;
					position: absolute;
					z-index: -1;
					border-radius: 30px;
					background: $color-white;
				}

				@include screen("small") {
					display: none;
				}
			}

			.wpmudev-button-text {
				display: none;

				@include screen("small") {
					display: block;
					text-align: center;
				}
			}

			&:hover, &:active {

				.wpmudev-button-icon {

					.wpmudev-icon path {
						fill: $color-red;
					}
				}
			}

			@include screen("small") {
				min-width: 60px;
				padding: 0 16px;
			}
		}

		// Button: Error log
		.button-view-log-list {
			min-width: 40px;
			padding: 0 !important;

			.wpmudev-button-icon {
				height: 26px;
				display: flex;
				flex-direction: column;
				align-items: center;
				justify-content: center;

				.wpmudev-icon {
					width: auto;
					height: 16px;

					path {
						fill: $color-red--light;
					}
				}

				@include screen("small") {
					display: none;
				}
			}

			.wpmudev-button-text {
				display: none;

				@include screen("small") {
					display: block;
					text-align: center;
				}
			}

			&:hover, &:active {

				.wpmudev-button-icon {

					.wpmudev-icon path {
						fill: $color-white;
					}
				}
			}

			@include screen("small") {
				min-width: 80px;
				padding: 0 14px !important;
			}
		}

		// Button: View email
		.button-view-email-list {
			min-width: 40px;
			padding: 0;

			.wpmudev-button-icon {
				height: 30px;
				display: flex;
				flex-direction: column;
				align-items: center;
				justify-content: center;

				.wpmudev-icon {
					width: 14px;
					height: auto;

					path {
						fill: $color-white;
					}
				}

				@include screen("small") {
					display: none;
				}
			}

			.wpmudev-button-text {
				display: none;

				@include screen("small") {
					display: block;
					text-align: center;
				}
			}

			@include screen("small") {
				min-width: 80px;
				padding: 0 16px;
			}
		}

		@include screen("medium") {
			padding: 19px 30px 20px;
		}
	}

	.wpmudev-box-body {
		position: relative;
		border-top: 1px solid $color-silver--alt;
		//background: #F6F7F8;

		.wpmudev-box-enabled,
		.wpmudev-box-disabled {
			flex-direction: column;
			align-items: center;
			justify-content: center;
			position: absolute;
			z-index: 1;
			top: 0;
			right: 0;
			bottom: 0;
			left: 0;
			border-radius: 0 0 5px 5px;
			background: #F6F7F8;
		}

		.wpmudev-box-enabled {
			display: none;
		}

		.wpmudev-box-disabled {
			display: flex;
		}
	}

	@include screen("medium") {
		margin-bottom: 30px;
	}
}
