.hustle-modal:not(.hustle-modal-image_only) {

	.hustle-modal-image_contain {
			
		@media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
		
			img {
				width: auto;
				height: auto;
				max-width: 100%;
				max-height: 100%;			
			}
			
			.hustle-modal-image {
				&_centercenter {
					top: 50%;
					left: 50%;
					transform: translate(-50%,-50%);
				}
				
				&_centertop, &_centercustom {
					left: 50%;
					transform: translate(-50%,0px);
				}
				
				&_centerbottom {
					left: 50%;
					transform: translate(-50%,0px);
					bottom: 0;
				}
				
				&_leftcenter {
					top: 50%;
					transform: translate(0px,-50%);
				}
				
				&_leftbottom {
					bottom: 0;
				}
				
				&_rightcenter {
					right: 0;
					top: 50%;
					transform: translate(0px,-50%);
				}
				
				&_righttop {
					right: 0;
				}
				
				&_rightbottom {
					right: 0;
					bottom: 0;
				}
				
			}
			
		}
	}

}