.wpmudev-ui .wpmudev-table {
	width: 100%;
	display: block;
	table-layout: fixed;
	margin: 30px 0;
	padding: 0;
	
	thead {
		display: none;
		background: #F2F2F2;

		tr {

			th {
				padding: 0 0 0 15px;
				color: $color-gray--dark;
				font: 500 14px/40px $font;
				letter-spacing: -0.29px;
				text-align: left;

				&:first-child {
					border-top-left-radius: 5px;
					border-bottom-left-radius: 5px;
				}

				&:last-child {
					padding: 0 15px;
					border-top-right-radius: 5px;
					border-bottom-right-radius: 5px;
				}
			}
		}

		@include screen("medium") {
			display: table-header-group;
		}
	}

	tbody {
		display: block;

		tr {
			display: block;
			border-bottom: 1px solid $color-silver--alt;

			th, td {
				display: block;
				padding: 0 0 10px;
				color: $color-gray--light;
				font: 400 13px/22px $font;
				letter-spacing: -0.25px;

				&:first-child {
					@include screen("medium") {
						padding-left: 0;
					}
				}

				&:not(:last-child) {
					@include screen("medium") {
						padding-right: 0;
					}
				}

				&:last-child {
					@include screen("medium") {
						padding-right: 0;
					}
				}

				@include screen("medium") {
					display: table-cell;
					padding: 7px 15px 10px;
					border-bottom: 1px solid $color-silver--alt;
				}
			}

			th {
				color: $color-gray--dark;
				font-family: $font--alt;
				font-weight: 700;
				letter-spacing: -0.15px;
				text-transform: uppercase;
			}

			&:first-child {

				td {

					@include screen("medium") {
						padding-top: 20px;
					}
				}
			}

			@include screen("medium") {
				display: table-row;
			}
		}

		@include screen("medium") {
			display: table-row-group;
			border-bottom: 0;
		}
	}

	tfoot {
		display: block;

		tr {
			display: block;

			td {
				display: flex;
				flex-wrap: wrap;
				flex-direction: column;
				padding: 30px 0 0;

				.wpmudev-button {
					flex: 1;
				}

				@include screen("medium") {
					display: table-cell;
				}
			}

			@include screen("medium") {
				display: table-row;
			}
		}

		@include screen("medium") {
			display: table-footer-group;
		}
	}

	&.wpmudev-hidden_table {
		display: none;
	}

	&.wpmudev-show_table {
		display: block;

		@include screen("medium") {
			display: table;
		}
	}

	&:first-child {
		margin-top: 0;
	}

	&:last-child {
		margin-bottom: 0;
	}

	@include screen("medium") {
		display: table;
	}
}

.wpmudev-ui .wpmudev-table .wpmudev-table_inner {
	width: 100%;
	display: block;
	table-layout: fixed;
	margin: 0;
	padding: 0;

	tbody {

		tr {
			border-bottom: 1px solid $color-silver--alt;

			&:first-child {
				
				td {
					padding-top: 0;
					padding-left: 0;
				}
			}

			&:last-child {
				border-bottom: 0;
				
				td {
					padding-right: 0;
					padding-bottom: 0;
					border-bottom: 0;
				}
			}
		}

		@include screen("medium") {
			border-bottom: 0;
		}
	}

	@include screen("medium") {
		display: table;
	}
}
