.hustle-modal:not(.hustle-modal-image_only) {
	
	.hustle-modal-image {
		position: relative;
		background: #00335b; // SAMPLE ONLY – Remove later

		img {
			width: 100%;
			height: 100%;
			display: block;
			-ms-interpolation-mode: bicubic;
		}

		// IMAGE Fit
		&.hustle-modal-image_fill {
			flex: 0 0 150px;
			
			img {
				position: absolute;
				object-fit: fill;
			}

			@include screen("small") {
				flex: 0 0 316px;
			}
		}

		&.hustle-modal-image_contain {
			flex: 0 0 150px;

			img {
				position: absolute;
				object-fit: contain;
			}

			@include screen("small") {
				flex: 0 0 316px;
			}
		}

		&.hustle-modal-image_cover {
			flex: 0 0 150px;

			img {
				position: absolute;
				object-fit: cover;
			}

			@include screen("small") {
				flex: 0 0 316px;
			}
		}

		&.hustle-modal-image_none {
			overflow: hidden;
			display: flex;
			flex: 0 0 auto;
			flex-direction: column;
			justify-content: center;

			img {
				width: auto;
				max-width: 100%;
				height: auto;
				max-height: 100%;
				flex: 0 1 auto;
				margin: 0 auto;
			}

			&.hustle-modal-mobile_hidden {
				display: none;

				@include screen("small") {
					display: flex;
				}
			}

			@include screen("small") {
				max-width: 50%;
				flex: 0 0 auto;
			}
		}

		&:not(.hustle-modal-image_none) {

			&:only-child {

				img {

					@include screen("small") {
						position: relative;
					}
				}

				@include screen("small") {
					flex: 1;
					max-height: 316px;
				}
			}
			
			&.hustle-modal-mobile_hidden {
				display: none;

				@include screen("small") {
					display: block;
				}
			}
		}

		// IMAGE Position
		&:not(.hustle-modal-image_fill):not(.hustle-modal-image_none) img {

			&.hustle-modal-image_lefttop {
				object-position: left top;
			}

			&.hustle-modal-image_leftcenter {
				object-position: left center;
			}

			&.hustle-modal-image_leftbottom {
				object-position: left bottom;
			}

			&.hustle-modal-image_centertop {
				object-position: center top;
			}

			&.hustle-modal-image_centercenter {
				object-position: center;
			}

			&.hustle-modal-image_centerbottom {
				object-position: center bottom;
			}

			&.hustle-modal-image_righttop {
				object-position: right top;
			}

			&.hustle-modal-image_rightcenter {
				object-position: right center;
			}

			&.hustle-modal-image_rightbottom {
				object-position: right bottom;
			}
		}
	}

	.hustle-size-small {

		.hustle-modal-image {

			&.hustle-modal-image_fill,
			&.hustle-modal-image_contain,
			&.hustle-modal-image_cover {
				
				@include screen("small") {
					flex: 0 0 150px;
				}
			}

			&.hustle-modal-image_none {

				@include screen("small") {
					max-width: unset;
				}
			}

			&:not(.hustle-modal-image_none) {

				&:only-child {
	
					@include screen("small") {
						flex: unset;
						max-height: unset;
					}
				}
			}
		}
	}
}

.hustle-modal.hustle-modal-image_only .hustle-modal-image {
	
	img {
		width: 100%;
		max-width: 100%;
		height: 100%;
		max-height: 100%;
		display: block;
		margin: 0 auto;
	}
}

// Featured Image Left Align
.hustle-modal:not(.hustle-modal-image_only) .hustle-modal-image.hustle-modal-image-left {
	order: 1;
}

// Featured Image Right Align
.hustle-modal:not(.hustle-modal-image_only) .hustle-modal-image.hustle-modal-image-right {
	order: 2;
}
