.hustle-modal .hustle-gdpr-box {
	display: flex;
	flex-wrap: wrap;
	margin: 15px 0;

	.hustle-gdpr-checkbox {
		flex: 0 0 20px;
		margin: 0 10px 0 0;
		padding: 0;
		border: 0;

		input {
			@extend %screen-reader-only;

			&:checked + span {
				border-color: transparent;

				&:before {
					color: $color-green; // SAMPLE ONLY
				}
			}
		}

		span[aria-hidden=true] {
			width: 20px;
			height: 20px;
			cursor: pointer;
			display: flex;
			flex-direction: column;
			align-items: center;
			justify-content: center;
			background-color: $color-white; // SAMPLE ONLY
			border: 2px solid transparent;
			transition: 0.3s ease;

			&.hustle-modal-optin_error {
				border-color: $color-red;
			}
			
			&:before {
				content: "\e925";
				display: block;
				color: $color-white; // SAMPLE ONLY
				speak: none;
				font-size: 16px;
				line-height: 20px;
				font-family: "wpdui-fi" !important;
				-webkit-font-smoothing: antialiased;
				-moz-osx-font-smoothing: grayscale;
				text-rendering: optimizeLegibility;
				text-transform: none;
			}
		}
	}

	.hustle-gdpr-content {
		flex: 1;
	}

	&:first-child {
		margin-top: 0;
	}

	&:last-child {
		margin-bottom: 0;
	}
}